.chip {
  --#{$prefix}chip-bg-color: #{$chip-default-bg-color-default};
  --#{$prefix}chip-bg-color-hover : #{$chip-default-bg-color-hover};
  --#{$prefix}chip-bg-color-focus : #{$chip-default-bg-color-focus};
  --#{$prefix}chip-bg-color-active : #{$chip-default-bg-color-active};
  padding: $chip-padding;
  color: $chip-color;
  background-color: var(--#{$prefix}chip-bg-color);
  font-size: $chip-font-size;
  border-radius: $chip-border-radius;
  border: none;
  display: inline-flex;
  align-items: center;
  column-gap: $chip-column-gap;

  &::after {
    content: "\eada";
    font-family: 'weno-icons' !important;
    font-size: $chip-icon-size;
    color: $chip-icon-color-default;
    speak: never;
    line-height: 1;
  }

  &:focus {
    background-color: var(--#{$prefix}chip-bg-color-focus);
  }

  &:hover {
    background-color: var(--#{$prefix}chip-bg-color-hover);
  }

  &:active {
    background-color: var(--#{$prefix}chip-bg-color-active);
  }

  &:hover,
  &:active {
    &::after {
      color: $chip-icon-color-state;
    }
  }

  &-counter {
    padding-right: $chip-counter-padding-right;

    &::after {
      all: unset;
    }
  }
  &-danger {
    --#{$prefix}chip-bg-color: #{$chip-danger-bg-color-default};
    --#{$prefix}chip-bg-color-hover : #{$chip-danger-bg-color-hover};
    --#{$prefix}chip-bg-color-focus : #{$chip-danger-bg-color-focus};
    --#{$prefix}chip-bg-color-active : #{$chip-danger-bg-color-active};

    &:focus {
      outline: $chip-danger-outline-focus;
    }
  }
}
