$chip-padding: $spacing-04 $spacing-04 $spacing-04 $spacing-08;
$chip-color: $gray-100;
$chip-font-size: $font-size-14;
$chip-border-radius: $border-radius;

$chip-default-bg-color-default: $gray-700;
$chip-default-bg-color-focus: $gray-700;
$chip-default-bg-color-hover: $gray-600;
$chip-default-bg-color-active: $gray-500;
$chip-danger-bg-color-default: $red-700;
$chip-danger-bg-color-focus: $red-700;
$chip-danger-bg-color-hover: $red-600;
$chip-danger-bg-color-active: $red-500;

$chip-danger-outline-focus: $spacing-04 solid $focus-ring-color-red;


$chip-icon-color-default: $gray-200;
$chip-icon-color-state: $gray-100;
$chip-column-gap: $spacing-04;
$chip-icon-size: 2rem;

$chip-counter-padding-right: $spacing-08;