:root {
	--#{$prefix}outline: #{$focus-outline-blue};
	--#{$prefix}outline-red: #{$focus-outline-red};
	--#{$prefix}outline-green: #{$focus-outline-green};
	--#{$prefix}outline-blue: #{$focus-outline-blue};
  --#{$prefix}menu-width: 0%;

  @each $name, $value in $spacing {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $elevation {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $radius {
    --#{$prefix}#{$name}: #{$value};
  }

  // Colors
  @each $name, $value in $grays {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $blues {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $reds {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $greens {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $oranges {
    --#{$prefix}#{$name}: #{$value};
  }

  @each $name, $value in $yellows {
    --#{$prefix}#{$name}: #{$value};
  }

  // Typography
  @each $name, $value in $font-colors {
    --#{$prefix}#{$name}: #{$value};
  }

  // Elevation
  @each $name, $value in $elevation-colors {
    --#{$prefix}#{$name}: #{$value};
  }

  // Forms
  --#{$prefix}form-state-border-color: #{$input-state-border-color};

  // Validation

  --#{$prefix}form-warning-color: #{$form-warning-color};
  --#{$prefix}form-warning-border-color: #{$form-warning-border-color};

  // Icons
  @each $name, $value in $icon-colors {
    --#{$prefix}icon-#{$name}: #{$value};
  }
}
