@mixin icon-color($color) {
	&::before {
    	color: map-get($icon-colors, $color);
	}
}

@mixin icon-size($size) {
	&::before {
    	font-size: map-get($icon-sizes, $size);
	}
}

@mixin icon-colors-hover($default, $hover) {
	color: $default;

	&:hover {
		color: $hover;
	}
}

@each $color, $value in $icon-colors {
	.icon-#{$color} {
		@include icon-color($color);
	}
}

@each $size, $value in $icon-sizes {
	.icon-#{$size} {
    	@include icon-size($size);
	}
}

@each $color, $values in $icon-colors-hover {
	$default-color: nth($values, 1);
	$hover-color: nth($values, 2);

	.icon-#{$color} {
		@include icon-colors-hover($default-color, $hover-color);
	}
}
