$font-family-sans-serif: "Tietoevry Sans";

$font-size-root: 62.5%; // 62.5% sets font size to 10px from browser 16px base (1rem = 10px)
$font-size-base: 1.6rem; // 1.6rem = 16px;

$text-primary: $gray-100;
$text-secondary: $gray-200;
$text-tertiary: $blue-300;
$text-primary-inverted: $white;
$text-secondary-inverted: $gray-800;
$text-danger: $red-300;
$text-warning: $orange-300;
$text-placeholder: $gray-400;
$text-disabled: $gray-400;
$text-tertiary-hover: $blue-100;

$body-color: $text-primary;

$font-size-32: 3.2rem;
$font-size-28: 2.8rem;
$font-size-24: 2.4rem;
$font-size-18: 1.8rem;
$font-size-16: 1.6rem;
$font-size-14: 1.4rem;
$font-size-12: 1.2rem;

$font-sizes: (
    5: $font-size-28,
    4: $font-size-24,
    3: $font-size-18,
    2: $font-size-16,
    1: $font-size-14,
);

$line-height-base: 1.5;
$line-height-sm: 1.25;
$line-height-lg: 2.4;
$line-height-16: 1.6rem;
$line-height-24: 2.4rem;
$line-height-20: 2rem;

$font-colors: (
  "text-primary": $text-primary,
  "text-secondary": $text-secondary,
  "text-tertiary": $text-tertiary,
  "text-primary-inverted": $text-primary-inverted,
  "text-secondary-inverted": $text-secondary-inverted,
  "text-danger": $text-danger,
  "text-warning": $text-warning,
  "text-placeholder": $text-placeholder,
  "text-disabled": $text-disabled,
  "text-tertiary-hover": $text-tertiary-hover,
);
