// WENO

$page-item-size: 2.8rem;
$page-link-line-height: $line-height-20;
$page-link-column-gap: $spacing-02;
$page-link-active-state-bg: $gray-600;

// Pagination

// scss-docs-start pagination-variables
$pagination-padding-y:              $spacing-04;
$pagination-padding-x:              $spacing-04;
// $pagination-padding-y-sm:           .25rem !default;
// $pagination-padding-x-sm:           .5rem !default;
// $pagination-padding-y-lg:           .75rem !default;
// $pagination-padding-x-lg:           1.5rem !default;

$pagination-font-size:              $font-size-14;

$pagination-color:                  $gray-100;
$pagination-bg:                     transparent;
$pagination-border-radius:          $radius-04;
$pagination-border-width:           0;
$pagination-margin-start:           0; // stylelint-disable-line function-disallowed-list
$pagination-border-color:           transparent;

$pagination-focus-color:            $gray-100;
$pagination-focus-bg:               transparent;
$pagination-focus-box-shadow:       $focus-ring-box-shadow;
// $pagination-focus-outline:          0 !default;

$pagination-hover-color:            $gray-100;
$pagination-hover-bg:               $gray-800;
$pagination-hover-border-color:     transparent; // Todo in v6: remove this?

$pagination-active-color:           $gray-100;
$pagination-active-bg:              $gray-700;
$pagination-active-border-color:    transparent;

$pagination-disabled-color:         $gray-400;
$pagination-disabled-bg:            transparent;
$pagination-disabled-border-color:  transparent;

// $pagination-transition:              color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;

// $pagination-border-radius-sm:       var(--#{$prefix}border-radius-sm) !default;
// $pagination-border-radius-lg:       var(--#{$prefix}border-radius-lg) !default;
// scss-docs-end pagination-variables