@include media-breakpoint-down(sm) {
  .menu-container {
    --#{$prefix}menu-gutter-x: 3.2rem;
    --#{$prefix}menu-width: 100%;
  }

  .menu-container ~ .weno-container {
    --#{$prefix}menu-width: 0%;
  }

  .main-container,
  .main-container-fluid,
  .container,
  .container-fluid {
    --#{$prefix}gutter-x: 3.2rem !important;
  }
}

@include media-breakpoint-only(sm) {
  .menu-container {
    --#{$prefix}menu-gutter-x: 2.4rem;
    --#{$prefix}menu-width: 8rem;
  }

  .menu-container ~ .weno-container {
    --#{$prefix}menu-width: 8rem;
  }

  .main-container,
  .main-container-fluid,
  .container,
  .container-fluid {
    --#{$prefix}gutter-x: 4.8rem !important;
  }
}

@include media-breakpoint-only(md) {
  .menu-container {
    --#{$prefix}menu-gutter-x: 2.4rem;
    --#{$prefix}menu-width: 8rem;
  }

  .menu-container ~ .weno-container {
    --#{$prefix}menu-width: 8rem;
  }

  .main-container,
  .main-container-fluid {
    --#{$prefix}gutter-x: 4.8rem !important;
  }
}

@include media-breakpoint-up(md) {
    .menu-container {
      --#{$prefix}menu-gutter-x: 2.4rem;
      --#{$prefix}menu-width: 28rem;
    }

    .menu-container ~ .weno-container {
      --#{$prefix}menu-width: 28rem;
    }
}

@include media-breakpoint-between(lg, xl) {
  .main-container,
  .main-container-fluid {
    --#{$prefix}gutter-x: 7.2rem !important;
  }
}

@include media-breakpoint-only(xl) {
  .main-container,
  .main-container-fluid {
    --#{$prefix}gutter-x: 7.2rem !important;
  }
}

@include media-breakpoint-up(xxl) {
  .main-container,
  .main-container-fluid {
    --#{$prefix}gutter-x: 9.6rem !important;
  }
}
