.day-number {
    font-size: $day-number-font-size;
    font-weight: $day-number-font-weight;
    line-height: $day-number-line-height;
    height: $day-number-size;
    width: $day-number-size;
    border-radius: $day-number-border-radius;
    color: $day-number-color;
    background-color: transparent;
    border: none;
    outline: none;

    &.selected {
        color: $day-number-color-selected;
        background-color: $day-number-selected-bg-default;

        &:focus {
            background-color: $day-number-selected-bg-focus;
        }
    }

    &:hover,
    &.selected:hover {
        background-color: $day-number-bg-hover;
    }

    &:active,
    &.selected:active {
        background-color: $day-number-bg-active;
    }
}

.dropdown > .day-number-menu {
    padding: $spacing-16;

    &.show {
        display: grid;
        grid-template-columns: repeat(7, auto);
        gap: $spacing-04;
    }
}
