.form-label {
  font-size: $font-size-14;
  font-weight: $font-weight-medium;

  &.required::after {
    content: "*";
    color: var(--#{$prefix}danger);
  }
}

.disabled > .form-label,
.form-label.disabled,
.disabled > .form-check-label,
.form-check-label.disabled {
  color: $text-disabled;
  cursor: not-allowed;
}