.form-select {
    &:hover:not([disabled]):not(.disabled) {
        border-color: var(--#{$prefix}form-state-border-color);
    }

    &:required:invalid,
    &.placeholder-text {
        color: $gray-600;
    }

    option {
        color: $gray-100;
    }

    & ~ .dropdown-menu {
        width: 100%;
        max-height: 24rem;
    }

    &[type='button'] {
        text-align: left;
    }

    &[type='text'] {
        @extend .form-control;
        &[readonly] {
            background-image: none;
        }
    }
}

.was-validated .form-select,
.form-select {
    &:valid,
    &.is-valid {
        &:not([multiple]):not([size]),
        &:not([multiple])[size='1'] {
            --#{$prefix}form-select-bg-icon: none;
        }
    }
}
