.form-control {
    &:hover:not([disabled]):not(.disabled) {
        border-color: var(--#{$prefix}form-state-border-color);
    }

    &[readonly]:not([disabled]):not(.disabled) {
        background-color: $form-control-readonly-bg-color;
        color: $form-control-readonly-color;
    }

    &[readonly]:not([disabled]):not(.disabled):not(:focus) {
        border-color: $form-control-readonly-border-color;
    }

    &[type='search'] {
        background-color: $form-control-search-bg;
        border: none;

        &::-webkit-search-cancel-button {
            -webkit-appearance: none;
        }
    }
}

textarea.form-control {
    resize: none;
    padding-right: 0;
}

.was-validated textarea.form-control:valid,
textarea.form-control.is-valid {
    background-image: none;
    padding-right: 0;
}
