.badge {
  --#{$prefix}badge-bg-color-informative: #{$badge-bg-color-informative};
  --#{$prefix}badge-bg-color-neutral: #{$badge-bg-color-neutral};
  --#{$prefix}badge-bg-color-danger: #{$badge-bg-color-danger};
  --#{$prefix}badge-bg-color-warning: #{$badge-bg-color-warning};
  --#{$prefix}badge-bg-color-success: #{$badge-bg-color-success};
  --#{$prefix}badge-bg-color-contagious: #{$badge-bg-color-contagious};

  white-space: nowrap;
  height: fit-content;
  display: inline-block;
  line-height: #{$badge-line-height};

  &-informative {
    background-color: var(--#{$prefix}badge-bg-color-informative);
  }

  &-neutral {
    background-color: var(--#{$prefix}badge-bg-color-neutral);
  }

  &-danger {
    background-color: var(--#{$prefix}badge-bg-color-danger);
  }

  &-warning {
    background-color: var(--#{$prefix}badge-bg-color-warning);
  }

  &-success {
    background-color: var(--#{$prefix}badge-bg-color-success);
  }

  &-contagious {
    background-color: var(--#{$prefix}badge-bg-color-contagious);
  }
}

.badge-small {
  @extend .badge;
  --#{$prefix}badge-padding-y: #{$badge-padding-y-small};
}
