// scss-docs-start theme-color-variables
$primary: $blue-300;
$secondary: $gray-800;
$success: $green-300;
$info: $blue-300;
$warning: $orange-300;
$danger: $red-300;
$light: $gray-100;
$dark: $gray-900;
// scss-docs-end theme-color-variables

// Prevent creating colors unused by our DS
$colors: (
  "black": $black,
  "white": $white,
);
$indigos: ();
$purples: ();
$pinks: ();
$teals: ();
$cyans: ();

$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark,
);