// scss-docs-start focus-ring-variables
$focus-ring-width:      0.4rem;
$focus-ring-opacity:    1;
$focus-ring-color-red:      $red-600;
$focus-ring-color-green:      $green-600;
$focus-ring-color-blue:      $blue-600;
$focus-ring-blur:       0;
$focus-ring-box-shadow: none;
$focus-outline-red: $focus-ring-width solid $focus-ring-color-red;
$focus-outline-green: $focus-ring-width solid $focus-ring-color-green;
$focus-outline-blue: $focus-ring-width solid $focus-ring-color-blue;
// scss-docs-end focus-ring-variables