.context-menu {
    min-width: 20rem;
    max-width: 35rem;
    top: 1rem;
    right: -4rem;

    & > li > .dropdown-item {
        color: $gray-100;

        &.disabled,
        &:disabled {
            color: var(--#{$prefix}dropdown-link-disabled-color);
            pointer-events: none;
            background-color: transparent;
            // Remove CSS gradients if they're enabled
            background-image: if($enable-gradients, none, null);
        }
    }
}

.user-context {
    display: flex;
    flex-direction: row;
    align-items: start;
    column-gap: $spacing-12;
    padding: $spacing-08;
}
