$spacing-00: 0.0rem;
$spacing-02: 0.2rem;
$spacing-04: 0.4rem;
$spacing-08: 0.8rem;
$spacing-12: 1.2rem;
$spacing-16: 1.6rem;
$spacing-24: 2.4rem;
$spacing-32: 3.2rem;
$spacing-48: 4.8rem;
$spacing-64: 6.4rem;

$spacing: (
  "spacing-00": $spacing-00,
  "spacing-02": $spacing-02,
  "spacing-04": $spacing-04,
  "spacing-08": $spacing-08,
  "spacing-12": $spacing-12,
  "spacing-16": $spacing-16,
  "spacing-24": $spacing-24,
  "spacing-32": $spacing-32,
  "spacing-48": $spacing-48,
  "spacing-64": $spacing-64,
);

$spacer: 1.6rem;
$spacers: (
  0: 0,
  1: $spacing-02,
  2: $spacing-04,
  3: $spacing-08,
  4: $spacing-12,
  5: $spacing-16,
  6: $spacing-24,
  7: $spacing-32,
  8: $spacing-48,
  9: $spacing-64,
);