@function getState($string) {
  // Avoid making new strings if we don't need to.
  @if $string == "warning" {
    @return "valid";
  }
  @return $string;
}

@each $state, $value in $form-validation-states {
  @include form-validation-state-selector(getState($state)) {
    ~ .#{$state}-feedback {
      display: flex;
    }
  }
}

@include form-validation-state-selector("warning") {
  ~ .warning-feedback {
    display: flex;
  }
}

*[class*="-feedback"] {
  align-items: flex-start;
  column-gap: 0.2rem;
  padding-bottom: 0.4rem;
  font-weight: 500;
  line-height: 20px;

  &::before {
    height: 20px;
  }
}

.informative-feedback,
.neutral-feedback {
  display: none;
  width: 100%;
  margin-top: 0;
  font-size: 1.4rem;
}

.neutral-feedback {
  color: $gray-200;

  &::before {
    content: url("./icons/feedback/feedback-neutral.svg");
  }
}

.informative-feedback {
  color: $info;

  &::before {
    content: url("./icons/feedback/feedback-info.svg");
  }
}

.invalid-feedback {
  &::before {
    content: url("./icons/feedback/feedback-invalid.svg");
  }
}

.valid-feedback {
  &::before {
    content: url("./icons/feedback/feedback-valid.svg");
  }
}

.warning-feedback {
  &::before {
    content: url("./icons/feedback/feedback-warning.svg");
  }
}



.was-validated .form-control.is-warning:valid, .form-control.is-warning {
  border-color: var(--#{$prefix}form-warning-border-color);
}

.was-validated .form-control:valid, .form-control.is-valid,
.was-validated .form-control:invalid, .form-control.is-invalid,
.was-validated .form-control.is-warning:valid, .form-control.is-warning,
.was-validated .form-select:valid, .form-select.is-valid,
.was-validated .form-select:invalid, .form-select.is-invalid,
.was-validated .form-select.is-warning:valid, .form-select.is-warning {
  &:active,
  &:hover,
  &:focus {
    border-color: var(--#{$prefix}form-state-border-color);
  }
}