.nav {
  --#{$prefix}nav-link-bg: #{$nav-link-bg-default};
  --#{$prefix}nav-link-border-radius: #{$nav-link-border-radius};
  --#{$prefix}nav-link-child-padding-left-x: #{$nav-link-child-padding-left-x};
  --#{$prefix}nav-link-row-gap: #{$nav-link-row-gap};
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  row-gap: var(--#{$prefix}nav-link-row-gap);
}

.nav-item {
  &-submenu {
    display: grid;
    grid-template-rows: 1fr;
    transition: grid-template-rows 300ms ease-in-out;

    & > * {
      overflow: hidden;
    }

    & * > .nav-link {
      padding: var(--#{$prefix}nav-link-padding-y)
        var(--#{$prefix}nav-link-padding-x) var(--#{$prefix}nav-link-padding-y)
        var(--#{$prefix}nav-link-child-padding-left-x);

      &:focus {
        outline-offset: -0.4rem;
      }
    }

    &[aria-hidden="true"] {
      grid-template-rows: 0fr;
      transition: grid-template-rows 300ms ease-in-out;
    }
  }
}

.nav-link {
  width: 100%;
  background-color: var(--#{$prefix}nav-link-bg);
  display: flex;
  justify-content: space-between;
  text-align: start;
  border-radius: var(--#{$prefix}nav-link-border-radius);
  cursor: pointer;

  &:focus-visible:not(.disabled) &:focus:not(.disabled) {
    --#{$prefix}nav-link-bg: #{$nav-link-bg-focus};
  }

  &:hover:not(.disabled) {
    --#{$prefix}nav-link-bg: #{$nav-link-bg-hover};
  }

  &:active:not(.disabled) {
    --#{$prefix}nav-link-bg: #{$nav-link-bg-active};
  }

  &[aria-current]:not([aria-current="false"]):not(.disabled),
  &.active:not(.disabled) {
    --#{$prefix}nav-link-bg: #{$nav-link-bg-selected};
    --#{$prefix}nav-link-font-weight: #{$nav-link-font-weight-active};
  }

  &-parent[aria-expanded]::after {
    content: "\eb29";
    font-family: 'weno-icons' !important;
    font-size: $nav-link-icon-size;
    color: $nav-link-icon;
    speak: never;
    line-height: 1;
    rotate: 0;
    transition: rotate 0.3s ease-in-out;
  }

  &-parent[aria-expanded="false"]::after {
    rotate: 180deg;
  }

  &.disabled {
    --#{$prefix}nav-link-bg: #{$nav-link-bg-disabled};
  }
}
