.floating-button {
  position: relative;

  input.form-control ~ .btn-icon-inset {
    position: absolute;
    top: 1.1rem;
    right: 1.2rem;
  }

  .form-select ~ .btn-icon-inset,
  input.form-control.is-valid ~ .btn-icon-inset {
    position: absolute;
    top: 1.1rem;
    right: 4rem;
  }

  .form-select,
  input.form-control.is-valid {
    padding-right: 6.8rem;
  }
}

.was-validated {
  .floating-button {
    input.form-control:valid ~ .btn-icon-inset {
      position: absolute;
      top: 1.1rem;
      right: 4rem;
    }

    input.form-control:valid {
      padding-right: 6.8rem;
    }
  }
}
