.dropdown {
    width: fit-content;
}

.dropdown-menu {
    overflow-y: auto;
    box-shadow: $dropdown-box-shadow;
    min-width: 15rem;
    max-width: 32rem;

    &.show {
        display: flex;
        flex-direction: column;
        row-gap: $spacing-04;
    }
}

.dropdown-menu-multiple {
    @extend .dropdown-menu;
    row-gap: $spacing-08;
    overflow: hidden;
    padding: 0;
    padding: $spacing-12 0;

    input[type="search"] {
        width: auto;
        margin: $spacing-02 $spacing-12 0 $spacing-12;
    }

    & > ul {
        display: flex;
        flex-direction: column;
        padding: 0 0 0 $spacing-12;
        text-align: left;
        list-style: none;
        margin: 0;
        overflow: hidden scroll;

        .form-check {
            padding: $spacing-04 0;
        }
    }

    &-footer {
        display: flex;
        justify-content: space-between;
        padding: $spacing-04 $spacing-12 0 $spacing-12;
        column-gap: $spacing-32;
        text-wrap: nowrap;
    }

    &.show {
        & > ul {
            row-gap: $spacing-08;
        }
    }
}

.dropdown-item {
    border-radius: $dropdown-inner-border-radius;
    text-wrap: wrap;

    &:focus-visible {
        background-color: transparent;
    }

    &.selected {
        display: flex;
        justify-content: space-between;
        background-color: $dropdown-link-active-bg;
        color: var(--#{$prefix}body-color);

        &:hover {
            background-color: $dropdown-link-selected-hover-bg;
        }

        &::after {
            margin-left: 1.2rem;
            content: '\eabc';
            font-family: 'weno-icons' !important;
            speak: never;
            line-height: 1;
            font-size: 2rem;
        }
    }
}

.dropdown-toggle {
    &::after {
        all: unset;
        content: '\eb29';
        font-family: 'weno-icons' !important;
        speak: never;
        font-size: $font-size-24;
        line-height: 1;
        rotate: 0;
        transition: rotate 0.3s ease-in-out;
    }

    &[aria-expanded='true']::after {
        rotate: 180deg;
    }
}
