.menu-container {
  width: var(--#{$prefix}menu-width);
  padding-right: calc(var(--#{$prefix}menu-gutter-x) * 0.5);
  padding-left: calc(var(--#{$prefix}menu-gutter-x) * 0.5);
  display: inline-flex;
  flex-direction: column;
  vertical-align: top;
}

.weno-container {
  width: calc(100% - var(--#{$prefix}menu-width));
  display: inline-flex;
  vertical-align: top;
  flex-wrap: wrap;
  overflow: hidden;
}

// Fluent width change for columns
*[class*="col-"] {
  transition: all 0.5s ease-in-out;
}

*[class*="container"] {
  transition: all 0.5s ease-in-out;
}

@if $enable-container-classes {
  // Single container class with breakpoint max-widths
  .main-container,
  // 100% wide container at all breakpoints
  .main-container-fluid {
    @include make-container($make-main-container-value);
    overflow: scroll;
    max-height: 100vh;
  }

  @each $breakpoint, $container-max-width in $container-max-widths {
    .main-container-#{$breakpoint} {
      @extend .main-container-fluid;
    }

    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      %responsive-main-container-#{$breakpoint} {
        max-width: $container-max-width;
      }

      // Extend each breakpoint which is smaller or equal to the current breakpoint
      $extend-breakpoint: true;

      @each $name, $width in $grid-breakpoints {
        @if ($extend-breakpoint) {
          .main-container#{breakpoint-infix($name, $grid-breakpoints)} {
            @extend %responsive-main-container-#{$breakpoint};
          }

          // Once the current breakpoint is reached, stop extending
          @if ($breakpoint == $name) {
            $extend-breakpoint: false;
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .weno-container {
    display: flex;
    width: 100%;

    > *[class*="col"] {
      width: 100%;
    }
  }
}

@include media-breakpoint-only(sm) {
  .weno-container {
    > *[class*="col"] {
      width: 100%;
    }
  }
}
