.nav-underline {
  --#{$prefix}nav-link-color: #{$gray-200};
  --#{$prefix}nav-link-border-radius: #{$nav-underline-border-radius};

  flex-direction: row;
  border-bottom: 1px solid $nav-underline-hover-border-color;

  .nav-link {
    padding: $nav-tabs-link-padding;

    &:hover:not(.active),
    &:focus:not(.active) {
      border-bottom-color: $nav-underline-hover-border-color;
      background-color: transparent;
    }

    &:active {
      background-color: transparent;
    }

    &:focus-visible {
      border: none;
      border-radius: $radius-04;
    }

    &.active {
      background-color: transparent;
    }
  }

  .tab-nav-button {
    margin-top: 0.2rem;
    padding: $tab-nav-button-padding;
    color: var(--#{$prefix}nav-link-color);
    background-color: transparent;
    border: none;
    border-radius: $tab-nav-button-border-radius;
    @include transition($pagination-transition);
    text-decoration: none;

    &:hover {
      background-color: $tab-nav-bg-hover;
    }

    &:active {
      background-color: $tab-nav-bg-active;
    }

    &.disabled,
    .disabled > &,
    &:disabled {
      color: var(--#{$prefix}text-disabled);
      pointer-events: none;
      background-color: transparent;
    }
  }
}
