[role="tooltip"] {
  @extend .tooltip;
  @extend .tooltip-inner;
  visibility: hidden;
  position: absolute;
  line-height: $tooltip-line-height;
  margin: 0;
}

.tooltip-toggle {
  position: relative;

  &:focus [role="tooltip"],
  &:hover [role="tooltip"] {
    visibility: visible;
    opacity: 1;
  }
}

.tooltip {
  &-top {
    margin-bottom: $tooltip-margin;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
  }

  &-bottom {
    margin-top: $tooltip-margin;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
  }

  &-left {
    margin-right: $tooltip-margin;
    right: 100%;
  }

  &-right {
    margin-left: $tooltip-margin;
    left: 100%;
  }
}
