.form-check {
    display: flex;
    align-items: start;
    padding: $form-check-padding-y-small 0;

    .form-check-input {
        margin-right: $form-check-input-margin-gap;
    }
}

.form-check-large {
    @extend .form-check;
    padding: $form-check-padding-y-large 0;
}

.form-check-reverse {
    display: flex;
    flex-direction: row-reverse;
    align-items: start;
    padding: $form-check-padding-y-large 0;

    .form-check-input {
        margin-left: $form-check-input-margin-gap;
    }

    .form-check-label {
        width: 100%;
        text-align: start;
    }
}

.form-check-child {
    @extend .form-check;
    padding-left: $form-check-child-padding-left;
}

.form-check-large-child {
    @extend .form-check-large;
    padding-left: $form-check-child-padding-left;
}

.form-check-reverse-child {
    @extend .form-check-reverse;
    padding-left: $form-check-child-padding-left;
}

.form-check-input + .form-check-label {
    cursor: pointer;
}

.form-check-input {
    min-height: $form-check-input-width;
    min-width: $form-check-input-width;
    margin: $form-check-input-margin-y 0;
    cursor: pointer;

    &[type='checkbox'] {
        &:focus {
            border-color: $form-check-input-color-focus;
        }

        &:hover {
            border-color: $form-check-input-color-hover;
        }

        &:active {
            border-color: $form-check-input-color-active;
        }

        &:disabled {
            border-color: $input-disabled-border-color;
            pointer-events: initial;
        }

        &:checked {
            &:focus {
                background-color: $form-check-input-color-focus;
            }

            &:hover {
                background-color: $form-check-input-color-hover;
            }

            &:active {
                background-color: $form-check-input-color-active;
            }

            &:disabled {
                border-color: $input-disabled-border-color;
                background-color: $form-check-input-bg-disabled;
                pointer-events: initial;
            }
        }
    }

    &[type='checkbox']:indeterminate {
        background-color: $form-check-input-indeterminate-bg-color;

        @if $enable-gradients {
            --#{$prefix}form-check-bg-image: #{escape-svg($form-check-input-indeterminate-bg-image)},
                var(--#{$prefix}gradient);
        } @else {
            --#{$prefix}form-check-bg-image: #{escape-svg(
                    $form-check-input-indeterminate-bg-image
                )};
        }

        &:focus {
            border-color: $form-check-input-indeterminate-color;
        }

        &:hover {
            border-color: $form-check-input-indeterminate-color-hover;
            --#{$prefix}form-check-bg-image: #{escape-svg(
                    $form-check-input-indeterminate-bg-image-hover
                )};
        }

        &:active {
            border-color: $form-check-input-indeterminate-color-active;
            --#{$prefix}form-check-bg-image: #{escape-svg(
                    $form-check-input-indeterminate-bg-image-active
                )};
        }

        &:disabled {
            border-color: $input-disabled-border-color;
            --#{$prefix}form-check-bg-image: #{escape-svg(
                    $form-check-input-indeterminate-bg-image-disabled
                )};
            pointer-events: initial;
        }
    }

    &[type='radio'] {
        background-color: $form-check-radio-checked-bg-color;

        &:focus {
            border-color: $form-check-input-color-focus;
        }

        &:hover {
            border-color: $form-check-input-color-hover;
        }

        &:active {
            border-color: $form-check-input-color-active;
        }

        &:disabled {
            border-color: $input-disabled-border-color;
            pointer-events: initial;
        }

        &:checked {
            background-color: $form-check-radio-checked-bg-color;

            &:hover {
                --#{$prefix}form-check-bg-image: #{escape-svg(
                        #{$form-check-radio-checked-bg-image-hover}
                    )};
            }

            &:active {
                --#{$prefix}form-check-bg-image: #{escape-svg(
                        #{$form-check-radio-checked-bg-image-active}
                    )};
            }

            &:disabled {
                --#{$prefix}form-check-bg-image: #{escape-svg(
                        #{$form-check-radio-checked-bg-image-disabled}
                    )};
                border-color: $input-disabled-border-color;
                pointer-events: initial;
            }
        }
    }

    &[disabled],
    &:disabled {
        ~ .form-check-label {
            cursor: not-allowed;
            color: $form-check-label-disabled-color;
        }
    }
}

.form-switch {
    padding-left: $form-switch-padding-start;

    .form-check-input {
        --#{$prefix}form-check-bg: #{$form-switch-bg-color};
        border-color: $form-switch-bg-color;
        height: $form-switch-height;
        margin-top: 0;
        margin-bottom: 0;

        &:focus {
            border-color: $form-switch-bg-color;
        }

        &:hover {
            border-color: $form-switch-bg-color-hover;
            background-color: $form-switch-bg-color-hover;
        }

        &:active {
            border-color: $form-switch-bg-color-active;
            background-color: $form-switch-bg-color-active;
        }

        &:disabled {
            background-color: $form-check-input-bg-disabled;
            border-color: $input-disabled-border-color;
            pointer-events: initial;
        }

        &:checked {
            background-color: $form-check-input-checked-bg-color;
            border-color: $form-check-input-checked-border-color;

            &:hover {
                background-color: $form-switch-bg-color-checked-hover;
                border-color: $form-switch-bg-color-checked-hover;
            }

            &:active {
                background-color: $form-switch-bg-color-checked-active;
                border-color: $form-switch-bg-color-checked-active;
            }

            &:disabled {
                background-color: $form-check-input-bg-disabled;
                border-color: $form-check-input-bg-disabled;
                pointer-events: initial;
            }
        }
    }

    &.form-check-reverse {
        .form-check-input {
            margin-left: $form-check-input-margin-gap;
        }
    }
}

.form-check-input-large {
    @extend .form-check-input;
    margin: 0;
    width: $form-check-input-large-size;
    height: $form-check-input-large-size;
    min-width: $form-check-input-large-size;
    min-height: $form-check-input-large-size;
}
