$radius-00: 0rem;
$radius-02: 0.2rem;
$radius-04: 0.4rem;
$radius-08: 0.8rem;
$radius-12: 1.2rem;

$radius: (
    "radius-00": $radius-00,
    "radius-02": $radius-02,
    "radius-04": $radius-04,
    "radius-08": $radius-08,
    "radius-12": $radius-12
);

// scss-docs-start border-radius-variables
$border-radius-sm: $radius-02;
$border-radius: $radius-04;
$border-radius-lg: $radius-08;
$border-radius-xl: $radius-12;
$border-radius-round: 50%;
$border-radius-pill: 80rem;
// scss-docs-end border-radius-variables
// fusv-disable
$border-radius-2xl: $border-radius-xl; // Deprecated in v5.3.0
// fusv-enable
