.breadcrumb-item {
  --#{$prefix}breadcrumb-item-color: #{$breadcrumb-item-color};
  --#{$prefix}breadcrumb-item-border-radius: #{$breadcrumb-item-border-radius};
  --#{$prefix}breadcrumb-item-line-height: #{$breadcrumb-item-line-height};
  border-radius: var(--#{$prefix}breadcrumb-item-border-radius);
  line-height: var(--#{$prefix}breadcrumb-item-line-height);
  display: flex;
  align-items: center;
  text-wrap: nowrap;

  & > a {
    color: var(--#{$prefix}breadcrumb-item-color);
    text-decoration: none;

    &:focus,
    &:hover,
    &:active {
      color: var(--#{$prefix}breadcrumb-item-color);
      text-decoration: underline;
    }
  }

  &.active > a {
    color: var(--#{$prefix}breadcrumb-item-active-color);
    pointer-events: none;
  }

  + .breadcrumb-item {
    &::before {
      content: "\eb28";
      font-family: 'weno-icons' !important;
      font-size: 2rem;
      color: $breadcrumb-divider-color;
      speak: never;
      line-height: 1;
      width: 2.4rem;
      height: 2rem;
    }
  }
}

.breadcrumb {
  width: fit-content;
  flex-wrap: nowrap;

  .dropdown-toggle {
    line-height: $line-height-20;
    background-color: transparent;
    border: none;

    &::after {
      content: "...";
      border: none;
    }
  }

  .dropdown-menu {
    --#{$prefix}dropdown-link-color: #{$gray-100};
    --#{$prefix}dropdown-link-hover-bg: transparent;
    --#{$prefix}dropdown-link-active-bg: transparent;
    --#{$prefix}dropdown-item-padding-x: #{$spacing-04};
    --#{$prefix}dropdown-item-padding-y: #{$spacing-04};
    --#{$prefix}dropdown-font-size: #{$breadcrumb-font-size};
    --#{$prefix}dropdown-min-width: 0;
    width: auto;
    left: 50%;
    transform: translateX(-50%);
  }

  .dropdown-item {
    line-height: var(--#{$prefix}breadcrumb-item-line-height);
    text-wrap: nowrap;

    &:hover {
      text-decoration: underline;
    }
  }
}
