@import "./utils/text-tokens";

.user-avatar {
    &-small {
        @extend .text-sm-500-secondary;
        margin: 0.4rem;
        line-height: 3.2rem;
        height: 3.2rem;
        width: 3.2rem;
        min-height: 3.2rem;
        min-width: 3.2rem;
    }

    &-default {
        @extend .text-std-500-secondary;
        margin: 0.2rem 0;
        line-height: 4rem;
        height: 4rem;
        width: 4rem;
        min-height: 4rem;
        min-width: 4rem;
    }

    &-default,
    &-small {
        text-align: center;
        vertical-align: middle;
        background-color: var(--weno-gray-800);
        border-radius: 50%;
        border: none;
    }
}

.patient-avatar {
    @extend .title-06-blue;
    background-color: var(--weno-blue-800);
    text-align: center;
    vertical-align: middle;
    border-radius: 50%;
    line-height: 8rem;
    height: 8rem;
    width: 8rem;

    &[class^="icon-"],
    &[class*=" icon-"] {
        font-size: 4rem;
        line-height: 8rem;
    }
}