// WENO
$form-feedback-waning-color: $warning;

$form-warning-color: $form-feedback-waning-color;
$form-warning-border-color: $orange-400;

// scss-docs-start form-feedback-variables
$form-feedback-margin-top:          0;
$form-feedback-font-size:           1.4rem;
// $form-feedback-font-style:          $form-text-font-style !default;
$form-feedback-valid-color:         $success;
$form-feedback-invalid-color:       $danger;

// $form-feedback-icon-valid-color:    $form-feedback-valid-color !default;
$form-feedback-icon-valid:          url("./icons/feedback/feedback-valid-inset.svg");
// $form-feedback-icon-invalid-color:  $form-feedback-invalid-color !default;
// $form-feedback-icon-invalid:        url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 12 12' width='12' height='12' fill='none' stroke='#{$form-feedback-icon-invalid-color}'><circle cx='6' cy='6' r='4.5'/><path stroke-linejoin='round' d='M5.8 3.6h.4L6 6.5z'/><circle cx='6' cy='8.2' r='.6' fill='#{$form-feedback-icon-invalid-color}' stroke='none'/></svg>") !default;
// $form-feedback-icon-invalid:        url("./icons/validation/validation-invalid.svg");
// scss-docs-end form-feedback-variables

// scss-docs-start form-validation-colors
// $form-valid-color:                  $form-feedback-valid-color !default;
$form-valid-border-color:           $input-state-border-color;
// $form-invalid-color:                $form-feedback-invalid-color !default;
// $form-invalid-border-color:         $form-feedback-invalid-color !default;
// scss-docs-end form-validation-colors

$form-validation-states: (
  "valid": (
    "color": var(--#{$prefix}form-valid-color),
    "icon": $form-feedback-icon-valid,
    "tooltip-color": #fff,
    "tooltip-bg-color": none,
    "focus-box-shadow": none,
    // default for invalid style to not override the default and state border colors
    "border-color": default,
  ),
  "invalid": (
    "color": var(--#{$prefix}form-invalid-color),
    "icon": none,
    "tooltip-color": none,
    "tooltip-bg-color": none,
    "focus-box-shadow": none,
    "border-color": var(--#{$prefix}form-invalid-border-color),
  ),
  "warning": (
    "color": var(--#{$prefix}form-warning-color),
    "icon": none,
    "tooltip-color": none,
    "tooltip-bg-color": none,
    "focus-box-shadow": none,
    "border-color": var(--#{$prefix}form-warning-border-color),
  ),
);