:focus-visible,
*[tabindex]:focus-visible,
[class^="form-control"]:focus-visible,
[class^="form-check"]:focus-visible,
[class^="form-select"]:focus-visible,
.btn:focus,
.btn.focus,
a[href]:focus-visible {
  outline: var(--#{$prefix}outline);
  outline-offset: 0;

  // &.input-validation-error {
  //   border-color: var(--lc-error-form-border-color);
  //   outline: $spacing-04 solid var(--lc-error-focus-outline-color);
  // }
}

.btn:focus,
.btn.focus {
  box-shadow: none;
}
