.btn {
    --#{$prefix}btn-width: fit-content;
    --#{$prefix}btn-column-gap: #{$spacing-08};
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: var(--#{$prefix}btn-width);
    column-gap: var(--#{$prefix}btn-column-gap);
    border: none;

    &-inset,
    &-icon-inset,
    &-inset-large,
    &-icon-inset-large {
        --#{$prefix}btn-width: auto;
        --#{$prefix}btn-padding-x: #{$spacing-00};
        --#{$prefix}btn-padding-y: #{$spacing-00};
        --#{$prefix}btn-font-size: #{$font-size-14};
        --#{$prefix}btn-color: #{$gray-200};
        --#{$prefix}btn-column-gap: #{$spacing-00};
        --#{$prefix}btn-font-weight: #{$font-weight-normal};
        --#{$prefix}btn-border-radius: #{$radius-04};
        padding-right: #{$spacing-04};

        &:hover {
            --#{$prefix}btn-color: #{$gray-100};
        }
    }

    &-inset,
    &-icon-inset {
        --#{$prefix}btn-line-height: #{$line-height-20};

        & > [class^='icon-'],
        [class*=' icon-'] {
            font-size: 2rem;
        }
    }

    &-inset-large,
    &-icon-inset-large {
        --#{$prefix}btn-line-height: #{$line-height-24};

        & > [class^='icon-'],
        [class*=' icon-'] {
            font-size: 2.4rem;
        }
    }

    &-icon-inset,
    &-icon-inset-large {
        padding-right: #{$spacing-00};
    }

    &::first-letter {
        text-transform: capitalize;
    }

    &-left-icon {
        --#{$prefix}btn-with-icon-padding: #{$spacing-08};
        padding-left: var(--#{$prefix}btn-with-icon-padding);
    }

    &-right-icon {
        --#{$prefix}btn-with-icon-padding: #{$spacing-08};
        padding-right: var(--#{$prefix}btn-with-icon-padding);
    }

    &:focus,
    &:focus-visible {
        color: var(--#{$prefix}btn-color);
    }

    &:focus-visible {
        background-color: var(--#{$prefix}btn-focus-bg);
    }

    &:disabled,
    &.disabled,
    fieldset:disabled & {
        pointer-events: initial;
    }

    &:active:disabled,
    &:active[disabled] {
        @extend .disabled;
    }
}

.btn-justified {
    @extend .btn;
    --#{$prefix}btn-width: 100%;
}

.btn-icon {
    @extend .btn;
    --#{$prefix}btn-padding-x: #{$spacing-08};
    --#{$prefix}btn-padding-y: #{$spacing-08};
}

.btn-icon-small {
    @extend .btn;
    --#{$prefix}btn-padding-x: #{$spacing-04};
    --#{$prefix}btn-padding-y: #{$spacing-04};
}

.btn-link {
    --#{$prefix}btn-padding-x: #{$spacing-00};
    --#{$prefix}btn-padding-y: #{$spacing-00};
    cursor: pointer;

    &:disabled,
    &.disabled {
        color: var(--#{$prefix}btn-disabled-color);
    }
}

$button-colors: (
    'primary': (
        'default-bg': $blue-300,
        'hover-bg': $blue-200,
        'active-bg': $blue-100,
        'color': $white,
        'outline': $focus-ring-color-blue,
    ),
    'secondary': (
        'default-bg': $gray-800,
        'hover-bg': $gray-700,
        'active-bg': $gray-600,
        'color': $gray-100,
        'outline': $focus-ring-color-blue,
    ),
    'success': (
        'default-bg': $green-300,
        'hover-bg': $green-200,
        'active-bg': $green-100,
        'color': $white,
        'outline': $focus-ring-color-green,
    ),
    'info': (
        'default-bg': transparent,
        'hover-bg': transparent,
        'active-bg': transparent,
        'color': transparent,
        'outline': transparent,
    ),
    'warning': (
        'default-bg': transparent,
        'hover-bg': transparent,
        'active-bg': transparent,
        'color': transparent,
        'outline': transparent,
    ),
    'danger': (
        'default-bg': $red-300,
        'hover-bg': $red-200,
        'active-bg': $red-100,
        'color': $white,
        'outline': $focus-ring-color-red,
    ),
    'light': (
        'default-bg': $gray-900,
        'hover-bg': $gray-800,
        'active-bg': $gray-700,
        'color': $gray-100,
        'outline': $gray-600,
    ),
    'dark': (
        'default-bg': $gray-300,
        'hover-bg': $gray-200,
        'active-bg': $gray-100,
        'color': $white,
        'outline': $gray-400,
    ),
);

@each $name, $values in $button-colors {
    .btn-#{$name} {
        --#{$prefix}btn-focus-bg: #{map-get($values, 'default-bg')};
        --#{$prefix}outline: #{$focus-ring-width solid map-get($values, 'outline')};
        @include button-variant(
            map-get($values, 'default-bg'),
            transparent,
            $color: map-get($values, 'color'),
            $hover-background: map-get($values, 'hover-bg'),
            $hover-border: transparent,
            $active-background: map-get($values, 'active-bg'),
            $active-border: transparent,
            $disabled-background: $gray-900,
            $disabled-border: transparent,
            $disabled-color: $gray-400
        );
    }
}

// override bootstrap classes which are not in use
@each $color, $value in $theme-colors {
    .btn-outline-#{$color} {
        @include button-outline-variant(transparent);
    }
}

$button-transparent-colors: (
    'primary': (
        'color-default': $blue-300,
        'color-active': $blue-100,
        'font-weight': $font-weight-normal,
    ),
    'secondary': (
        'color-default': $gray-100,
        'color-active': $gray-100,
        'font-weight': $font-weight-bold,
    ),
);

@each $name, $values in $button-transparent-colors {
    .btn-transparent-#{$name} {
        --#{$prefix}btn-focus-bg: transparent;
        --#{$prefix}btn-font-weight: #{map-get($values, 'font-weight')};
        --#{$prefix}btn-padding-x: #{$btn-transparent-padding-x};

        @include button-variant(
            transparent,
            transparent,
            $color: map-get($values, 'color-default'),
            $hover-background: $gray-800,
            $hover-border: transparent,
            $hover-color: map-get($values, 'color-active'),
            $active-background: $gray-700,
            $active-border: transparent,
            $active-color: map-get($values, 'color-active'),
            $disabled-background: transparent,
            $disabled-border: transparent,
            $disabled-color: $gray-400
        );
    }
}

$button-icon-colors: (
    'primary': (
        'default-bg': transparent,
        'hover-bg': $gray-800,
        'active-bg': $gray-700,
        'disabled-bg': transparent,
        'color-default': $blue-300,
        'color-active': $blue-100,
    ),
    'secondary': (
        'default-bg': transparent,
        'hover-bg': $gray-800,
        'active-bg': $gray-700,
        'disabled-bg': transparent,
        'color-default': $gray-100,
        'color-active': $gray-100,
    ),
    'background': (
        'default-bg': $gray-800,
        'hover-bg': $gray-700,
        'active-bg': $gray-600,
        'disabled-bg': $gray-900,
        'color-default': $gray-100,
        'color-active': $gray-100,
    ),
);

@each $name, $values in $button-icon-colors {
    .btn-icon-#{$name} {
        --#{$prefix}btn-focus-bg: transparent;
        @include button-variant(
            map-get($values, 'default-bg'),
            transparent,
            $color: map-get($values, 'color-default'),
            $hover-background: map-get($values, 'hover-bg'),
            $hover-border: transparent,
            $hover-color: map-get($values, 'color-active'),
            $active-background: map-get($values, 'active-bg'),
            $active-border: transparent,
            $active-color: map-get($values, 'color-active'),
            $disabled-background: map-get($values, 'disabled-bg'),
            $disabled-border: transparent,
            $disabled-color: $gray-400
        );
    }
}

@include media-breakpoint-down(sm) {
    .btn,
    .btn-justified {
        &:not(.btn-inset):not(.btn-icon-inset) {
            --#{$prefix}btn-padding-x: #{$btn-padding-x-mobile};
            --#{$prefix}btn-padding-y: #{$btn-padding-y-mobile};
        }
    }
    .btn-icon,
    .btn-icon-small {
        --#{$prefix}btn-icon-size: 4.8rem;
        --#{$prefix}btn-padding-x: #{$btn-icon-padding-mobile};
        --#{$prefix}btn-padding-y: #{$btn-icon-padding-mobile};
    }

    .btn-icon-clear,
    .btn-clear {
        --#{$prefix}btn-padding-x: #{$spacing-00};
        --#{$prefix}btn-padding-y: #{$spacing-00};
    }
}

.btn-transparent-primary {
    &:disabled,
    [disabled] {
        --#{$prefix}btn-font-weight: #{$font-weight-normal};
    }
}
