.form-field {
    .form-control,
    .form-select {
        margin-top: $form-field-caption-gap;
    }

    .form-label ~ .form-text {
        margin-top: $form-field-control-gap;
    }

    .dropdown {
        width: 100%;
    }

    .dropdown-menu {
        min-width: auto;
        max-width: none;
        max-height: 30rem;
    }
}
