// Accordion
// WENO
$accordion-box-shadow: $shadow-small-default;
$accordion-box-shadow-hover: $shadow-small-hover;
$accordion-button-bg-hover: $gray-900;
$accordion-body-border-top: 1px solid $gray-800;
$accordion-collapse-padding-y: $spacing-16;

// scss-docs-start accordion-variables
$accordion-padding-y: $spacing-16;
$accordion-padding-x: $spacing-24;
// $accordion-color:                         var(--#{$prefix}body-color) !default;
// $accordion-bg:                            var(--#{$prefix}body-bg) !default;
// $accordion-border-width:                  var(--#{$prefix}border-width) !default;
$accordion-border-color: $gray-800;
$accordion-border-radius: var(--#{$prefix}border-radius-lg);
$accordion-inner-border-radius: subtract($accordion-border-radius, $accordion-border-width);

$accordion-body-padding-y: 0;
$accordion-body-padding-x: $accordion-padding-x;

$accordion-button-padding-y: $spacing-16;
$accordion-button-padding-x: $spacing-16;
// $accordion-button-color:                  var(--#{$prefix}body-color) !default;
// $accordion-button-bg:                     var(--#{$prefix}accordion-bg) !default;
$accordion-transition:
    $btn-transition,
    border-radius 0.15s ease;
$accordion-button-active-bg: var(--#{$prefix}accordion-bg);
$accordion-button-active-color: var(--#{$prefix}body-color);

$accordion-button-focus-border-color: $input-focus-border-color;
$accordion-button-focus-box-shadow: none;

