.accordion {
    box-shadow: $accordion-box-shadow;
    border-radius: $accordion-border-radius;
    height: fit-content;

    &:hover {
        box-shadow: $accordion-box-shadow-hover;
    }

    &-header {
        position: relative;

        .btn-group,
        .btn-icon-group {
            position: absolute;
            z-index: 10;
            top: 1rem;
            right: 1.2rem;
        }
    }

    &-button {
        display: flex;
        column-gap: $spacing-08;
        align-items: start;
        position: relative;
        overflow: hidden;
        line-height: $line-height-base;
        transition: border-radius 0s ease-in-out;

        &:focus,
        &:not(.collapsed) {
            box-shadow: none;
        }

        &::before {
            content: '\eb29';
            font-family: 'weno-icons' !important;
            speak: never;
            font-size: $font-size-24;
            line-height: 1;
            rotate: 0;
            transition: rotate 0.3s ease-in-out;
        }

        &[aria-expanded='false'] {
            border-radius: 0.4rem;
            transition: border-radius 0.1s 0.3s ease-in-out;
        }
        &[aria-expanded='true']::before {
            rotate: 180deg;
        }

        &::after {
            all: unset;
        }

        &:hover {
            background-color: $accordion-button-bg-hover;
        }

        &:focus-visible {
            outline: $focus-outline-blue;
        }

        .help-text {
            color: var(--weno-gray-200);
            font-size: $font-size-14;
            line-height: $line-height-20;
            font-weight: $font-weight-normal;
        }
    }

    &-collapse {
        display: grid;
        grid-template-rows: 0fr;
        transition:
            grid-template-rows 0.3s ease-in-out,
            padding-top 0.3s ease-in-out,
            padding-bottom 0.3s ease-in-out;
        padding: 0;

        & > * {
            transition: all 0.3s ease-in-out;
            overflow: hidden;
        }

        &.show {
            padding: $accordion-collapse-padding-y 0;
            grid-template-rows: 1fr;
            border-top: $accordion-body-border-top;
        }
    }
}

.btn-icon-group,
.btn-group {
    & ~ .accordion-button {
        padding-right: 12.4rem;
    }
}

.collapse-arrow {
    rotate: 0;
    transition: all 0.3s ease-in-out;

    &.rotate {
        rotate: 180deg;
    }
}
