// List group

// scss-docs-start list-group-variables
// $list-group-color:                  var(--#{$prefix}body-color) !default;
// $list-group-bg:                     var(--#{$prefix}body-bg) !default;
// $list-group-border-color:           var(--#{$prefix}border-color) !default;
$list-group-border-width:           0;
$list-group-border-radius:          var(--#{$prefix}border-radius);

$list-group-item-padding-y:         $spacing-08;
$list-group-item-padding-x:         $spacing-08;
// fusv-disable
// $list-group-item-bg-scale:          -80% !default; // Deprecated in v5.3.0
// $list-group-item-color-scale:       40% !default; // Deprecated in v5.3.0
// fusv-enable

$list-group-hover-bg:               $gray-900;
$list-group-active-color:           $gray-100;
$list-group-active-bg:              $gray-800;
// $list-group-active-border-color:    $list-group-active-bg !default;

// $list-group-disabled-color:         var(--#{$prefix}secondary-color) !default;
// $list-group-disabled-bg:            $list-group-bg !default;

$list-group-action-color:           $gray-100;
$list-group-action-hover-color:     $gray-100;

// $list-group-action-active-color:    var(--#{$prefix}body-color) !default;
$list-group-action-active-bg:       $gray-800;
// scss-docs-end list-group-variables