$elevation-primary: $blue-000;
$elevation-scrim: $blue-000-50;

$elevation-colors: (
  "elevation-primary": $elevation-primary,
  "elevation-scrim": $elevation-scrim,
);

$shadow-small-default: 0 0.4rem 0.8rem rgba($elevation-primary, 0.08);
$shadow-small-hover: 0 0.4rem 1.6rem rgba($elevation-primary, 0.12);
$shadow-large: 0 0.8rem 2.4rem rgba($elevation-primary, 0.12);
$shadow-context-menu: 0 0.4rem 1.2rem rgba($elevation-primary, 0.16);
$shadow-bottom-bar: 0 -0.4rem 1.6rem rgba($elevation-primary, 0.24);
$shadow-modal-light: 0 0.4rem 2.4rem rgba($elevation-primary, 0.24);
$shadow-modal-scrim: 0 0.4rem 2.4rem rgba($elevation-primary, 0.32);

$elevation: (
  "shadow-small-default": $shadow-small-default,
  "shadow-small-hover": $shadow-small-hover,
  "shadow-large": $shadow-large,
  "shadow-context-menu": $shadow-context-menu,
  "shadow-bottom-bar": $shadow-bottom-bar,
  "shadow-modal-light": $shadow-modal-light,
  "shadow-modal-scrim": $shadow-modal-scrim,
);

// scss-docs-start box-shadow-variables
$box-shadow: $shadow-small-hover; // set up as default because it kind of fit between sm and lg
$box-shadow-sm: $shadow-small-default;
$box-shadow-lg: $shadow-large;
// scss-docs-end box-shadow-variables
