.pagination {
  --#{$prefix}page-link-column-gap: #{$page-link-column-gap};
  --#{$prefix}pagination-active-bg: #{$page-link-active-state-bg};
  column-gap: var(--#{$prefix}page-link-column-gap);
}

.page-link {
  position: relative;
  min-width: $page-item-size;
  height: $page-item-size;
  line-height: $page-link-line-height;
  font-variant-numeric: lining-nums tabular-nums;
  text-align: center;
  cursor: pointer;

  &:hover {
    background-color: var(--#{$prefix}pagination-hover-bg);
  }

  &:active {
    background-color: var(--#{$prefix}pagination-active-bg);
  }

  &[aria-current]:not([aria-current="false"]) {
    @extend .active;
  }

  &.dots {
    pointer-events: none;

    &::before {
      content: "...";
    }
  }

  &.disabled,
  .disabled > &,
  &:disabled {
    color: var(--#{$prefix}text-disabled);
    pointer-events: none;
    background-color: transparent;
  }
}

