// Weno
$form-check-input-margin-gap: 0.8rem;
$form-check-padding-y-small: 0.4rem;
$form-check-padding-y-large: 0.8rem;
$form-check-child-padding-left: 2.4rem;

$form-check-input-large-size: 2.4rem;
$form-check-input-margin-y: 0.2rem;
$form-check-input-color-hover: $blue-200;
$form-check-input-color-active: $blue-100;
$form-check-input-color-focus: $blue-300;
$form-check-input-bg-disabled: $gray-400;

$form-check-label-disabled-color: $gray-400;

$form-check-radio-checked-bg-color: $white;
$form-check-radio-checked-color-hover: $blue-200;
$form-check-radio-checked-bg-image-hover: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='2.5' fill='#{$form-check-radio-checked-color-hover}'/></svg>");
$form-check-radio-checked-color-active: $blue-100;
$form-check-radio-checked-bg-image-active: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='2.5' fill='#{$form-check-radio-checked-color-active}'/></svg>");
$form-check-radio-checked-bg-image-disabled: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='2.5' fill='#{$form-check-input-bg-disabled}'/></svg>");

$form-check-input-indeterminate-color-hover: $blue-200;
$form-check-input-indeterminate-bg-image-hover: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><rect x='2' y='2' width='16' height='16' rx='2' fill='#{$form-check-input-indeterminate-color-hover}' /></svg>");
$form-check-input-indeterminate-color-active: $blue-100;
$form-check-input-indeterminate-bg-image-active: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><rect x='2' y='2' width='16' height='16' rx='2' fill='#{$form-check-input-indeterminate-color-active}' /></svg>");
$form-check-input-indeterminate-bg-image-disabled: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><rect x='2' y='2' width='16' height='16' rx='2' fill='#{$form-check-input-bg-disabled}' /></svg>");

$form-switch-bg-color: $gray-600;
$form-switch-bg-color-hover: $gray-500;
$form-switch-bg-color-active: $gray-400;
$form-switch-bg-color-checked-hover: $blue-200;
$form-switch-bg-color-checked-active: $blue-100;
$form-switch-height: 2.4rem;

// Bootstrap

// scss-docs-start form-check-variables
$form-check-input-width: 2rem;
// $form-check-min-height:                   $font-size-base * $line-height-base !default;
$form-check-padding-start: 0;
$form-check-margin-bottom: 0;
// $form-check-label-color:                  null !default;
// $form-check-label-cursor:                 null !default;
// $form-check-transition:                   null !default;

$form-check-input-active-filter: none;

// $form-check-input-bg:                     $input-bg !default;
// $form-check-input-border:                 var(--#{$prefix}border-width) solid var(--#{$prefix}border-color) !default;
$form-check-input-border-radius: $radius-04;
// $form-check-radio-border-radius:          50% !default;
// $form-check-input-focus-border:           $input-focus-border-color !default;
// $form-check-input-focus-box-shadow:       $focus-ring-box-shadow !default;

$form-check-input-checked-color: $blue-300;
// $form-check-input-checked-bg-color:       $component-active-bg !default;
// $form-check-input-checked-border-color:   $form-check-input-checked-bg-color !default;

$form-check-radio-checked-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='2.5' fill='#{$form-check-input-checked-color}'/></svg>");

$form-check-input-indeterminate-color: $blue-300;
$form-check-input-indeterminate-bg-color: $white;
$form-check-input-indeterminate-border-color: $gray-600;
$form-check-input-indeterminate-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><rect x='2' y='2' width='16' height='16' rx='2' fill='#{$form-check-input-indeterminate-color}' /></svg>");

$form-check-input-disabled-opacity: 1;
$form-check-label-disabled-opacity: 1;
$form-check-btn-check-disabled-opacity: 1;

// $form-check-inline-margin-end:    1rem !default;
// scss-docs-end form-check-variables

// scss-docs-start form-switch-variables
$form-switch-color: $white;
$form-switch-width: 4rem;
$form-switch-padding-start: 0;
$form-switch-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='3' fill='#{$form-switch-color}'/></svg>");
// $form-switch-border-radius:       $form-switch-width !default;
$form-switch-transition: background-position 0.15s ease-in-out !default;

$form-switch-focus-color: $white;
$form-switch-focus-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='3' fill='#{$form-switch-focus-color}'/></svg>");

$form-switch-checked-color: $white;
$form-switch-checked-bg-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='3' fill='#{$form-switch-checked-color}'/></svg>");
// $form-switch-checked-bg-position: right center !default;
// scss-docs-end form-switch-variables
