.title {
    &-01-300-primary {
        font-size: $font-size-28;
        font-weight: $font-weight-light;
        line-height: $line-height-sm;
        color: var(--#{$prefix}text-primary);
    }

    &-02-500-primary {
        font-size: $font-size-24;
        font-weight: $font-weight-medium;
        line-height: $line-height-sm;
        color: var(--#{$prefix}text-primary);
    }

    &-03-500-primary {
        font-size: $font-size-18;
        font-weight: $font-weight-medium;
        line-height: $line-height-24;
        color: var(--#{$prefix}text-primary);

        &-inverted {
            font-size: $font-size-18;
            font-weight: $font-weight-medium;
            line-height: $line-height-24;
            color: var(--#{$prefix}text-primary-inverted);
        }
    }

    &-04-500-primary {
        font-size: $font-size-16;
        font-weight: $font-weight-medium;
        line-height: $line-height-base;
        color: var(--#{$prefix}text-primary);

        &-inverted {
            font-size: $font-size-16;
            font-weight: $font-weight-medium;
            line-height: $line-height-base;
            color: var(--#{$prefix}text-primary-inverted);
        }
    }

    &-05-400-uppercase {
        &-primary {
            font-size: $font-size-14;
            font-weight: $font-weight-normal;
            line-height: $line-height-20;
            color: var(--#{$prefix}text-primary);
            letter-spacing: 1px;
            text-transform: uppercase;
        }

        &-secondary {
            font-size: $font-size-14;
            font-weight: $font-weight-normal;
            line-height: $line-height-20;
            color: var(--#{$prefix}text-secondary);
            letter-spacing: 1px;
            text-transform: uppercase;
        }
    }

    &-06-blue {
        font-size: $font-size-32;
        font-weight: $font-weight-bold;
        line-height: $line-height-base;
        color: var(--#{$prefix}blue-600);
    }
}

.text {
    &-std {
        &-400 {
            &-primary {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-primary);

                &-inverted {
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-primary-inverted);
                }
            }

            &-secondary {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-secondary);

                &-inverted {
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-secondary-inverted);
                }
            }

            &-placeholder {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-placeholder);
            }

            &-disabled {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-disabled);
            }

            &-danger {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-danger);
            }

            &-underline {
                &-primary {
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-primary);
                    text-decoration: underline;

                    &-inverted {
                        font-size: $font-size-16;
                        font-weight: $font-weight-normal;
                        line-height: $line-height-base;
                        color: var(--#{$prefix}text-primary-inverted);
                        text-decoration: underline;
                    }
                }

                &-secondary {
                    &-inverted {
                        font-size: $font-size-16;
                        font-weight: $font-weight-normal;
                        line-height: $line-height-base;
                        color: var(--#{$prefix}text-secondary-inverted);
                        text-decoration: underline;
                    }
                }

                &-tertiary {
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-tertiary);
                    text-decoration: underline;

                    &-hover {
                        font-size: $font-size-16;
                        font-weight: $font-weight-normal;
                        line-height: $line-height-base;
                        color: var(--#{$prefix}text-tertiary-hover);
                        text-decoration: underline;
                    }
                }

                &-disabled {
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-disabled);
                    text-decoration: underline;
                }
            }

            &-italic {
                &-secondary {
                    font-style: italic;
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-secondary);
                }
            }
        }

        &-500 {
            &-primary {
                font-size: $font-size-16;
                font-weight: $font-weight-medium;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-primary);
            }

            &-secondary {
                font-size: $font-size-16;
                font-weight: $font-weight-medium;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-secondary);
            }
        }

        &-700 {
            &-primary {
                font-size: $font-size-16;
                font-weight: $font-weight-bold;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-primary);
            }
        }
    }

    &-sm {
        &-400 {
            &-primary {
                font-size: $font-size-14;
                font-weight: $font-weight-normal;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-primary);

                &-inverted {
                    font-size: $font-size-14;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-20;
                    color: var(--#{$prefix}text-primary-inverted);
                }
            }

            &-secondary {
                font-size: $font-size-14;
                font-weight: $font-weight-normal;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-secondary);
            }

            &-disabled {
                font-size: $font-size-14;
                font-weight: $font-weight-normal;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-disabled);
            }

            &-underline {
                &-primary {
                    font-size: $font-size-14;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-20;
                    color: var(--#{$prefix}text-primary);
                    text-decoration: underline;
                }
            }

            &-italic {
                &-secondary {
                    font-style: italic;
                    font-size: $font-size-14;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-20;
                    color: var(--#{$prefix}text-secondary);
                }
            }
        }

        &-500 {
            &-primary {
                font-size: $font-size-14;
                font-weight: $font-weight-medium;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-primary);
            }

            &-secondary {
                font-size: $font-size-14;
                font-weight: $font-weight-medium;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-secondary);
            }

            &-disabled {
                font-size: $font-size-14;
                font-weight: $font-weight-medium;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-disabled);
            }

            &-danger {
                font-size: $font-size-14;
                font-weight: $font-weight-medium;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-danger);
            }

            &-warning {
                font-size: $font-size-14;
                font-weight: $font-weight-medium;
                line-height: $line-height-20;
                color: var(--#{$prefix}text-warning);
            }
        }
    }

    &-xs {
        &-400 {
            &-secondary {
                font-size: $font-size-12;
                font-weight: $font-weight-normal;
                line-height: $line-height-16;
                color: var(--#{$prefix}text-secondary);
            }

            &-disabled {
                font-size: $font-size-12;
                font-weight: $font-weight-normal;
                line-height: $line-height-16;
                color: var(--#{$prefix}text-disabled);
            }

            &-danger {
                font-size: $font-size-12;
                font-weight: $font-weight-normal;
                line-height: $line-height-16;
                color: var(--#{$prefix}text-danger);
            }
        }
    }
}

.button {
    &-std {
        &-400 {
            &-disabled {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-disabled);
            }

            &-tertiary {
                font-size: $font-size-16;
                font-weight: $font-weight-normal;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-tertiary);

                &-hover {
                    font-size: $font-size-16;
                    font-weight: $font-weight-normal;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-tertiary-hover);
                }
            }
        }

        &-500 {
            &-primary {
                font-size: $font-size-16;
                font-weight: $font-weight-medium;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-primary);

                &-inverted {
                    font-size: $font-size-16;
                    font-weight: $font-weight-medium;
                    line-height: $line-height-base;
                    color: var(--#{$prefix}text-primary-inverted);
                }
            }

            &-disabled {
                font-size: $font-size-16;
                font-weight: $font-weight-medium;
                line-height: $line-height-base;
                color: var(--#{$prefix}text-disabled);
            }
        }
    }
}
